/*----------------------  ACCORDION -----------------------*/
ul.accordion {
    margin: 1em 0;
    overflow: auto;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    padding: 0;

    >li {
        list-style-type: none;
        padding: 0;
        margin: .5em 0;
	background: $color__sombreado;
	@include borde(5px);

    }

    button {
        width: 100%;
        background: none;
        text-align: left;
        border: none;
        padding: 1em;
        &:focus {
	    border: none;
	    outline: none;
        }
    }
}

.accordion-panel {
    display: none; //comment this line to show and test
    height: auto;
    min-height: 1px;
    overflow-x: hidden;
    border-top: 1px solid white;
    padding: 1em 1.5em;
    font-size: .9em;
    p, li {
		margin: .25em 0;
    }

    p:first-child {
		margin-top: 0;
    }
    p:last-child {
		margin-bottom: 0;
    }
	
    &.resumen {	    
		@include vector-bg-with-fallback(resumen);
		background-position: 2em 1em;
		background-size: 60px;
		padding-left: 100px;
		@media screen and (min-width: $break__small) {
		    background-size: 80px;
		    padding-left: 130px;
		}
    }

    &.audio {
		text-align: center;
		font-size: .8em;
		audio {
		    width: 80%;
		    margin: 0 auto;
		    display: block;
		}
		p.meta {
		    margin: 1em auto;
		    
		}
		p.descargar-audio {
		    a {
			border: none;
		    }
		    
		    margin: 1em auto;
		}
	}


}

.mmtools {
    display: none;

    @media screen and (min-width: $break__small) {
        display: block;
        padding: 0;
        margin: -10px 0 0;
        width: auto;
        position: absolute;
        top: 100px;
        right: 1em;
        z-index: 1;
    }

    .label{
        color: #999;
        font-size: .6em;
    }
    a{
        text-decoration: none;
    }
}
.zeno_font_resizer_container{
    display: inline-block;
    position: relative;
    margin: 0;

    .zeno_font_resizer{
        margin: 0;
        line-height: 1;
        span {
            display: flex;
            align-items: baseline;
        }
    }
    a{
        margin: 0 .25em;
        color: #777;
        border: none;
        font-weight: 700;
        &:hover{
            text-decoration: underline;
